<template>
  <div class="content">
    <el-button @click="goldChange = true"
               style="margin-top:100px"
               type="primary">金价换算</el-button>
    <br>(目前只做了这个功能)
    <el-dialog title="金价换算"
               :visible.sync="goldChange"
               width="30%">
      <span class="gcTitle">提示：充的人民币越多，点券比例越高，<br>全充100美元最划算</span>
      <el-form :label-position="'right'">
        <el-form-item label="5美元个数："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.five"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="10美元个数："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.ten"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="20美元个数："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.twenty"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="30美元个数："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.thirty"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="50美元个数："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.fifty"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="100美元个数："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.oneHundred"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="拍卖行1000点券价格："
                      label-width="200px">
          <el-input-number v-model="goldChangeFrom.oneThousand"
                           :step="100"
                           :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="需要的人民币："
                      label-width="200px">
          <el-input placeholder="点击计算获取"
                    v-model="goldChangeFrom.RMB"
                    style="width:60%"
                    :disabled="true">
          </el-input>
        </el-form-item>
        <el-form-item label="可获得的点券数："
                      label-width="200px">
          <el-input placeholder="点击计算获取"
                    v-model="goldChangeFrom.num"
                    style="width:60%"
                    :disabled="true">
          </el-input>
        </el-form-item>
        <el-form-item label="人民币兑金币比例："
                      label-width="200px">
          <el-input placeholder="在上面输入拍卖点券价格"
                    v-model="goldChangeFrom.proportion"
                    style="width:60%"
                    :disabled="true">
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button type="primary"
                   @click="compute">计 算</el-button>
        <el-button @click="goldChange = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  props: {
    msg: String,
  },
  data() {
    return {
      goldChange: false,
      goldChangeFrom: {
        five: 0,
        ten: 0,
        twenty: 0,
        thirty: 0,
        fifty: 0,
        oneHundred: 0,
        oneThousand: 0, //1000点券价格
        num: null, //点券总数
        proportion: null, //比例
        RMB: null, //总人民币
      },
      huilv: null, //汇率
    }
  },
  mounted(){
    this.showDia()
  },
  methods: {
    showDia() {
      //汇率
      axios
        .get('https://api.it120.cc/gooking/forex/rate?fromCode=CNY&toCode=USD ')
        .then((res) => {
          console.log(res.data.data)
          if (res.data.data.rate) {
            this.huilv = res.data.data.rate
          } else {
            this.showDia()
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    compute() {
      let g = this.goldChangeFrom
      //总点券数
      g.num =
        g.five * 1000 +
        g.ten * 2200 +
        g.twenty * 4600 +
        g.thirty * 7000 +
        g.fifty * 11800 +
        g.oneHundred * 25000

      //RMB总数
      g.RMB =
        (g.five * 5 +
          g.ten * 10 +
          g.twenty * 20 +
          g.thirty * 30 +
          g.fifty * 50 +
          g.oneHundred * 100) *
        this.huilv

      //比例
      if (g.oneThousand !== 0 && g.RMB !== 0) {
        g.proportion = ((g.num / 1000) * g.oneThousand) / g.RMB
      }
      this.$message({
        showClose: true,
        message: '计算成功！',
        type: 'success',
      })
    },
  },
}
</script>

