.content {
  width: 100%;
  height: 100%;
  text-align: center;
}
.gcTitle {
  text-align: center;
  color: red;
  font-size: 22px;
  font-weight: bold;
  width: 100%;
  display: block;
  margin: 15px 0px;
}
